import Vue from 'vue'
import Vuex from 'vuex'
import majors from './majors.json'
import librals from './librals.json'
import axio from 'axios'

const axios = axio.create({
  baseURL: 'https:///lecture.yjdev.com/api'
})

Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    title: 'ÏàòÍ∞ïÏã†Ï≤≠ ÎπàÏûêÎ¶¨ ÏïåÎûå',
    token: null,
    majors,
    librals,
    lectures: null,
    myLectures: []
  },
  getters: {
    title ({ title }) { return title },
    majors ({ majors }) { return majors },
    librals ({ librals }) { return librals },
    lectures ({ lectures }) { return lectures },
    myLectures ({ myLectures }) { return myLectures }
  },
  mutations: {
    setTitle (state, title) {
      state.title = title
    },
    setLectures (state, lectures) {
      state.lectures = lectures
    },
    setMyLectures (state, lectures) {
      console.log('setMyLectures', lectures)
      state.myLectures = lectures
    },
    setToken (state, token) {
      state.token = token
    }
  },
  actions: {
    getLectures ({ commit }, { id, gubun }) {
      console.log()
      axios.get(`/lectures/${id}?gubun=${gubun}`)
        .then(({ data }) => {
          console.log('getLectures', data)
          commit('setLectures', data)
        })
    },
    loadMyLectures ({ state, commit }, id) {
      const token = state.token
      axios.get(`/my/${token}`)
        .then(({ data }) => commit('setMyLectures', data))
    },
    register ({ state, commit }, { major, lecture, gubun }) {
      console.log(major, lecture, gubun)
      const { myLectures, token } = state
      console.log(myLectures)
      if (myLectures.find(l => l.id === lecture.id)) {
        console.log('Ïù¥ÎØ∏ Îì±Î°ùÎêú Í∞ïÏùò')
      }
      return axios.post('/register', { token, major, lecture, gubun })
        .then(({ data }) => {
          console.log(data)
          commit('setMyLectures', data)
        })
    },
    remove ({ state, commit }, lecture) {
      const { token, myLectures } = state
      console.log(lecture)
      commit('setMyLectures', myLectures.filter(l => l.id !== lecture.id))
      axios.post(`/remove/`, { token, lecture })
        .then(({ data }) => {
          commit('setMyLectures', data)
          console.log(data)
        })
    }
  }
})



