<template lang="pug">
v-dialog(v-model="dialog"  max-width="600px")
  v-btn.mr-0(@click="dialog = true" slot="activator" flat) 
    v-icon.mr-2 add
  v-card
    v-card-title.headline ÏïåÎûå Ï∂îÍ∞ÄÌïòÍ∏∞
    v-card-text.pa-0
      v-container(fluid grid-list-lg)
        v-layout(row wrap)
          v-flex(xs6)
            v-radio-group(v-model="gubun" row)
              v-radio(label="Ï†ÑÍ≥µ" value="1")
              v-radio(label="ÍµêÏñë" value="2")
          
          v-flex(xs12)
            v-select(:items="majors" :filter="customFilter" item-text="major" v-model="majorSelect" :label="label" autocomplete)
          v-flex(v-if="lectures" xs12)
            v-select(:items="lectures" :filter="customFilter" item-text="name" v-model="lectureSelect" label="Í∞ïÏùò ÏÑ†ÌÉùÌïòÍ∏∞" autocomplete)
              template(slot="item" slot-scope="data")
                v-list-tile-content
                  v-list-tile-title(v-text="data.item.name")
                  v-list-tile-sub-title {{ data.item.professor }} / {{ data.item.info }} / Ïù∏Ïõê: {{ data.item.people }}

    v-card-actions
      v-spacer
      v-btn(flat :disabled="disabled" @click="add") Ï∂îÍ∞Ä

</template>

<script>
export default {
  data () {
    return {
      dialog: false,
      majorSelect: null,
      lectureSelect: null,
      gubun: '1',
      customFilter (item, queryText, itemText) {
        const hasValue = val => val != null ? val : ''
        const text = hasValue(item.major)
        const query = hasValue(queryText)
        return text.toString()
          .toLowerCase()
          .indexOf(query.toString().toLowerCase()) > -1
      },
      loading: false
    }
  },
  computed: {
    majors () {
      return this.gubun === '1' ? this.$store.getters.majors : this.$store.getters.librals
    },
    label () {
      return this.gubun === '1' ? 'Ï†ÑÍ≥µ ÏÑ†ÌÉùÌïòÍ∏∞' : 'ÍµêÏñë ÏÑ†ÌÉùÌïòÍ∏∞'
    },
    lectures () { return this.$store.getters.lectures },
    disabled () { return this.loading || !this.majorSelect || !this.lectureSelect }
  },
  watch: {
    gubun (id) {
      this.lectureSelect = null
      this.$store.commit('setLectures', null)
    },
    majorSelect (id) {
      console.log('majorSelect', id)
      this.lectureSelect = null
      this.$store.dispatch('getLectures', { id, gubun: this.gubun })
    }
  },
  methods: {
    add () {
      if (this.disabled) return

      const { majorSelect, lectureSelect, gubun } = this
      this.loading = true
      this.$store.dispatch('register', { major: majorSelect, lecture: lectureSelect, gubun })
        .then(() => {
          this.loading = false
          this.dialog = false
        })
    }
  }
}
</script>

<style scoped>

</style>


