import Vue from 'vue'
import { initializeApp, messaging } from 'firebase'
import App from './App'
import router from './router'
import store from './store'

import {
  Vuetify,
  VApp,
  VNavigationDrawer,
  VFooter,
  VList,
  VBtn,
  VIcon,
  VGrid,
  VToolbar,
  VCard,
  VDialog,
  VSelect,
  VRadioGroup
} from 'vuetify'
import '../node_modules/vuetify/src/stylus/app.styl'

Vue.use(Vuetify, {
  components: {
    VApp,
    VNavigationDrawer,
    VFooter,
    VList,
    VBtn,
    VIcon,
    VGrid,
    VToolbar,
    VCard,
    VDialog,
    VSelect,
    VRadioGroup
  },
  theme: {
    primary: '#90CAF9',
    secondary: '#BBDEFB',
    accent: '#7986CB',
    error: '#DD2C00',
    warning: '#FFEB3B',
    info: '#2196f3',
    success: '#4caf50'
  }
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App),
  created () {
    initializeApp({
      messagingSenderId: '99989017353'
    })

    const message = messaging()
    navigator.serviceWorker.register('/static/firebase-messaging-sw.js')
      .then((registration) => {
        console.log('serviceWorker registration')
        return message.useServiceWorker(registration)
      }).then(() => {
        return message.requestPermission()
      })
      .then(() => {
        console.log('Notification permission granted.')
        return message.getToken()
      })
      .then(token => {
        this.$store.commit('setToken', token)
        console.log(token)
      })
      .then(() => {
        this.$store.dispatch('loadMyLectures')
      })
      .catch(err => {
        console.log('Unable to get permission to notify.', err)
      })

    message.onMessage(({ notification }) => {
      const { title, body } = notification
      this.$store.dispatch('loadMyLectures')
      console.log('onMessage: ', `${title} ${body}`)
      alert(body)
    })
  }
})



